.@{css-prefix}divider {
  margin: 0 4px;
  color: #999;
  display: inline-block;
  vertical-align: middle;
  height: 10px;
  width: 1px;
  background: lighten(@gray-color, 5%);
}

.tbd-inline {
  display: inline-block;
  vertical-align: middle;
}

a.tbd-inline,
a.tbd-link {
  color: @link-color;
  text-decoration: @link-hover-decoration;
  outline: none;

  &:hover {
    color: @link-hover-color;
  }

  &:active {
    color: @link-active-color;
  }

  &:active,
  &:hover {
    outline: 0;
    text-decoration: underline;
  }

  &[disabled] {
    color: #ccc;
    cursor: not-allowed;
    pointer-events: none;
  }
}
