@confirm-prefix-cls: ~'@{css-prefix}confirm';

.@{confirm-prefix-cls} {

  .tbd-dialog-header {
    display: none;
  }

  .tbd-dialog-body {
    padding: 30px 40px;
  }

  .@{confirm-prefix-cls}-body {
    position: relative;
    padding-top: 8px;
    padding-left: 42px;
    min-height: 28px;

    .@{confirm-prefix-cls}-title {
      color: @base-color;
      font-size: 14px;
    }

    .@{confirm-prefix-cls}-content {
      font-size: @font-size-base;
      color: @gray-color;
      margin-top: 8px;
    }

    .tbdi {
      position: absolute;
      left: 0;
      top: 0;
      font-size: 32px;
    }

    .tbdi-exclamation-circle {
      color: @error-color;
    }

    .tbdi-question-circle {
      color: @warning-color;
    }

    .tbdi-check-circle {
      color: @success-color;
    }

    .tbdi-info-circle {
      color: @info-color;
    }
  }

  .@{confirm-prefix-cls}-btns {
    margin-top: 30px;
    text-align: center;

    button + button {
      margin-left: 8px;
    }
    button {
      line-height: 30px;
      min-width: 80px;
      height: 32px;
    }
  }
}
