// mixins for fixing IE bug
// ------------------------

// Opacity
.opacity(@opacity) {
  opacity: @opacity;
  // IE8 filter
  @opacity-ie: (@opacity * 100);
  filter: ~"alpha(opacity=@{opacity-ie})";
}

// inline-block
.inline-block() {
  display: inline-block;
  // ie7 inline-block
  *display: inline;
  *zoom: 1;
}
