@input-number-prefix-cls: ~'@{css-prefix}input-number';

.@{input-number-prefix-cls} {
  display: inline-block;
  position: relative;

  .@{input-prefix-cls} {
    padding-right: 25px;
  }

  &-handle-wrap {
    position: absolute;
    right: 1px;
    top: 1px;
    bottom: 1px;
    width: 20px;
    border-left: 1px solid @border-color;
    // .opacity(0);
    // transition: opacity 0.3s linear;
    //
    // &:hover {
    //   .opacity(1);
    // }
  }

  &-handle {
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 50%;
    border: none;
    background: none;
    color: @gray-color;
    line-height: 18px;

    &-down {
      border-top: 1px solid @border-color;
    }

    &:hover {
      color: @primary-color;
    }
  }

  .@{iconfont-css-prefix} {
    .iconfont-size-under-12px(8px);
    font-weight: bold;
  }

  // 不同尺寸
  &-sm {
    .@{input-prefix-cls} {
      .input(@height-sm; @font-size-base);
      padding-right: 20px;
    }

    .@{input-number-prefix-cls}-handle {
      line-height: @height-sm / 2;
    }
  }

  &-lg {
    .@{input-prefix-cls} {
      .input(@height-lg; @font-size-lg);
      padding-right: 20px;
    }

    .@{input-number-prefix-cls}-handle {
      line-height: @height-lg / 2;
    }
  }

  // 不同状态
  &-success {
    .@{input-prefix-cls} {
      border-color: @success-color;
    }
  }

  &-error {
    .@{input-prefix-cls} {
      border-color: @error-color;
    }
  }

  &-warning {
    .@{input-prefix-cls} {
      border-color: @warning-color;
    }
  }

  &-disabled {
    .@{input-number-prefix-cls}-handle {
      &-wrap {
        border-left-color: @border-focus-color;
      }

      cursor: @cursor-disabled;

      &:hover {
        color: @gray-color;
      }
    }
  }
}
