// Layout
.layout-fixed {
  .container-fixed();

  @media (min-width: @screen-sm-min) {
    width: @container-sm;
  }

  @media (min-width: @screen-md-min) {
    width: @container-md;
  }

  @media (min-width: @screen-lg-min) {
    width: @container-lg;
  }
}

.layout-fluid {
  .container-fixed()
}

// Grid system
.tbd-row {
  .make-row();
  display: block;
}

.tbd-col {
  position: relative;
  display: block;
  float: left; 
}

.make-grid-columns();
.make-grid();
