.input(@height, @font-size) {
  padding-top: (@height - 2 - (@font-size * 1.5)) / 2;
  padding-bottom: (@height - 2 - (@font-size * 1.5)) / 2;
  height: @height;
  font-size: @font-size;
  line-height: 1.5;
}

// input status
// == when focus or actived
.active(@color: @form-focus-border-color) {
  border-color: @color;
}

.focus(@color: @form-focus-border-color) {
  border-color: @color;
}

.disabled() {
  &,
  &:focus {
    border-color: @form-disabled-border-color;
    background: @form-disabled-bg;
    color: lighten(@gray-color, 5%);
    cursor: @cursor-disabled;
  }
}

.search-radius(@height: @height) {
  .@{input-prefix-cls} {
    border-radius: @height / 2px;
  }

  button.@{input-prefix-cls}-wrap-extra {
    border-top-right-radius: @height / 2px;
    border-bottom-right-radius: @height / 2px;
  }
}

.form-control-validation(@text-color: @base-color; @border-color: @form-border-color; @background-color: @base-bg) {
  .@{css-prefix}form-explain,
  .@{css-prefix}form-split {
    color: @text-color;
  }
  // 输入框的不同校验状态
  .@{css-prefix}input {
    border-color: @border-color;

    &:not([disabled]):hover {
      border-color: @border-color;
    }
  }

  .@{css-prefix}input-group-extra {
    color: @text-color;
    border-color: @border-color;
    background-color: @background-color;
  }

  .has-feedback {
    color: @text-color;
  }

  // ant-select
  .@{select-prefix-cls} {
    &-selection {
      border-color: @border-color;
    }
    &-arrow {
      color: @text-color;
    }
  }

  // ant-datepicker
  .@{calendar-prefix-cls}-picker-icon:after {
    color: @text-color;
  }

  // ant-timepicker
  .@{timepicker-prefix-cls}-picker-icon:after {
    color: @text-color;
  }

  // ant-input-number
  .@{input-number-prefix-cls} {
    border-color: @border-color;
    &:not([disabled]):hover {
      border-color: @border-color;
    }
  }
}
