@calendar-prefix-cls: ~'@{css-prefix}calendar';

.@{calendar-prefix-cls} {
  // 日期选择器触发按钮
  &-btn {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    .input(@height, @font-size-base);
    padding-left: 10px;
    padding-right: 30px;
    border: 1px solid @border-color;
    border-radius: @border-radius-base;
    cursor: pointer;

    &-icon {
      position: absolute;
      right: 0;
      top: 0;
      width: 30px;
      text-align: center;
      line-height: @height - 2px;
      color: @gray-color;
    }

    &:hover &-icon {
      color: @primary-color;
    }

    &-active {
      border-color: @border-focus-color;
    }
  }

  // 日期选择器
  & {
    position: absolute;
    z-index: 999;
    padding-bottom: 10px;
    border: 1px solid @border-color;
    background: #fff;
    box-shadow: @box-shadow;
  }

  &-content {
    position: relative;
    .clearfix;
  }

  &-table {
    float: left;
    margin: 0 8px;
    font-size: 12px;

    caption {
      height: 36px;
      line-height: 36px;
      font-size: 14px;
      text-align: center;
    }

    thead {
      > tr {
        border: 1px solid @border-color;
        background: @light-bg;
      }
      th {
        padding: 4px 10px;
        line-height: 20px;
        color: @base-color;
        text-align: center;
        cursor: pointer;
        font-weight: normal;
      }
    }

    td {
      padding: 4px 11px;
      width: 36px;
      box-sizing: border-box;
      text-align: center;
      line-height: 20px;
      white-space: nowrap;
      cursor: pointer;

      &:hover {
        color: @primary-color;
      }
    }
  }

  td&-item {
    &-disabled {
      color: @gray-color;
      cursor: default;

      &:hover {
        color: @gray-color;
      }
    }

    &-active {
      background: url('@{img}/calendar_all.png') -24px 2px no-repeat;
      color: #fff;

      &:hover {
        color: #fff;
      }
    }
  }

  td&-range {
    &-first,
    &-active,
    &-end {
      background: url('@{img}/calendar_all.png') -60px 2px no-repeat;

      &:hover {
        color: @base-color;
      }
    }
    &-active {
      background-position: -96px 2px;
      color: #fff;

      &:hover {
        color: #fff;
      }
    }
    &-end {
      background-position: -133px 2px;
    }
  }

  &-prev,
  &-next {
    position: absolute;
    top: 8px;
    font-size: 18px;
    font-family: 'tbdicon';
    color: @gray-color;
    font-style: normal;
    .opacity(0.5);
    cursor: pointer;

    &:hover {
      .opacity(1);
    }
  }
  &-prev {
    left: 10px;

    &:after {
      content: '\e619';
    }
  }
  &-next {
    right: 10px;

    &:after {
      content: '\e616';
    }
  }

  // 底部
  &-footer {
    .clearfix;
    padding: 10px 10px 0;
    margin-top: 10px;
    border-top: 1px solid @border-color;

    &-content {
      float: left;

      &-row {
        margin-bottom: 5px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .@{input-prefix-cls} {
      width: 85px;
    }

    &-btns {
      float: right;
    }
    .@{btn-prefix-cls} {
      margin-left: 10px;
    }
  }

  // 下拉菜单样式调整
  .@{dropdown-prefix-cls} {
    &-wrap .@{dropdown-prefix-cls} {
      min-width: 0;
    }

    &-wrap .@{input-prefix-cls} {
      width: 36px;
      cursor: pointer;
    }

    &-menu {
      max-height: 132px;
    }

    &-menu-item {
      padding: 2px 10px;
      text-align: center;
    }
  }
  & &-year-dropdown,
  & &-month-dropdown {
    line-height: 1.5;

    .@{input-prefix-cls} {
      position: relative;
      top: -2px;
      width: 48px;
      .input(26px; @font-size-base);
    }
  }
  & &-month-dropdown {

    .@{input-prefix-cls} {
      width: 36px;
    }
  }

  // 区间日期带小时、分钟
  &-time {
    .@{calendar-prefix-cls}-footer-btns {
      padding-top: 19px;
    }
  }
}
