@transfer-perfix-cls: ~'@{css-prefix}transfer';

.@{transfer-perfix-cls} {
  position: relative;
  .clearfix;
  margin-bottom: 20px;

  &-btns {
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -47px 0 0 -29px;

    .@{btn-prefix-cls} {
      margin: 10px 0;
      display: block;
    }
  }

  &-item {
    margin-bottom: @gutter-base;
    border: 1px solid @border-color;
    box-sizing: border-box;

    &-header {
      padding: 0 20px;
      height: 40px;
      background: @light-bg;
      font-size: 12px;
      line-height: 40px;
      .clearfix;

      h3 {
        float: left;
        font-size: 14px;
        font-weight: normal;
      }
    }

    &-extra {
      float: right;

      .@{label-prefix-cls} {
        margin-right: 0;
      }
    }

    &-search {
      padding: 10px 10px 0;
      box-sizing: border-box;
    }

    &-content {
      box-sizing: border-box;
      padding: 10px 0;
      height: 250px;
      overflow: auto;

      .@{label-prefix-cls} {
        padding: 0 20px;
        line-height: 26px;
        transition: background 0.3s ease;

        &:hover {
          background-color: lighten(@base-bg, 5%);
        }
      }

      .@{tree-prefix-cls} {
        padding: 0 10px;
      }
    }

    &-with-search &-content {
      height: 204px;
    }
  }

  & &-item {
    margin-bottom: 0;
  }

  & &-item-wrap {
    float: left;
    padding-left: 50px;
    box-sizing: border-box;
    width: 50%;

    &:first-child {
      padding-right: 50px;
      padding-left: 0;
    }
  }

  & &-btns + &-item-wrap {
    padding-right: 50px;
    padding-left: 0;
  }

  // 带表格
  &-table {
    width: 100%;
    border-collapse: collapse;
    border: none;
    table-layout: fixed;

    th {
      padding: 0 5px;
      text-align: left;
      font-weight: normal;

      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }

    td {
      padding: 0 5px;
      text-align: left;
      font-weight: normal;
      line-height: 26px;

      &:first-child {
        padding-left: 20px;
      }
      &:last-child {
        padding-right: 20px;
      }
    }

    .@{label-prefix-cls} {
      position: relative;
      padding: 0 0 0 15px;
      margin: 0;
      display: block;

      &-text {
        display: block;
        .ellipsis;
      }
    }

    .@{checkbox-prefix-cls} {
      position: absolute;
      left: 0;
      top: 5px;
    }

    &-body {
      tr {
        cursor: pointer;
      }

      tr:hover {
        background-color: lighten(@base-bg, 5%);

        .@{checkbox-prefix-cls} {
          border-color: @radio-hover-border-color;
        }
      }
    }

    tr&-row {
      &-checked,
      &-checked:hover {
        color: @radio-checked-color;

        .@{label-prefix-cls}-text {
          color: @radio-checked-color;
        }

        .@{checkbox-prefix-cls} {
          border-color: @radio-checked-border-color;
          background: @radio-checked-bg;

          &:after {
            display: block;
          }
        }
      }

      &-disabled,
      &-disabled:hover {
        cursor: @cursor-disabled;
        color: @radio-disabled-color;
        background-color: transparent;

        .@{label-prefix-cls}-text {
          color: @radio-disabled-color;
        }

        .@{checkbox-prefix-cls} {
          border-color: @radio-disabled-border-color;
          background: @radio-disabled-bg;
          cursor: @cursor-disabled;
        }

        .@{checkbox-prefix-cls}:after {
          border-color: @radio-disabled-border-color;
        }
      }
      &-disabled .@{label-prefix-cls}:hover {
        background-color: transparent;
        cursor: @cursor-disabled;
      }
    }
  }

  .ellipsis {
    .ellipsis;
  }
}
