@spin-prefix-cls: ~"@{css-prefix}spin";

.@{spin-prefix-cls} {
  display: inline-block;
  color: #fff;
  line-height: @spin-size;

  &-circle {
    display: none;
    box-sizing: border-box;
    width: @spin-size;
    height: @spin-size;
    border: 3px solid #fff;
    border-color: #fff rgba(255, 255, 255, 0.5) rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    animation: loadingCircle 1s infinite linear;
  }

  &-text {
    display: inline;
  }

  :root &-circle {
    display: block;
  }
  :root &-text {
    display: none;
  }

  //不同尺寸
  &-sm {
    line-height: @spin-size-sm;
  }
  &-sm &-circle{
    width: @spin-size-sm;
    height: @spin-size-sm;
  }

  &-lg {
    line-height: @spin-size-lg;
  }
  &-lg &-circle{
    width: @spin-size-lg;
    height: @spin-size-lg;
    border-width: 6px;
  }

  //模块中的loading
  &-wrap {
    position: relative;
  }
  &-mask {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 500;
    width: 100%;
    height: 100%;
    background: #373737;
    opacity: 0.5;
    filter: alpha(opacity=50);
  }
  &-content {
    position: absolute;
    left: 0;
    top: 50%;
    z-index: 500;
    margin-top: -(@spin-size/2);
    width: 100%;
    text-align: center;
  }

  //页面laoding
  &-page {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
  }
}

