@import "../mixins/btn.less";

@btn-prefix-cls: ~"@{css-prefix}btn";

.@{btn-prefix-cls} {
  & {
    display: inline-block;
    box-sizing: border-box;
    height: @height;
    padding: @btn-padding;
    border: 1px solid @btn-border-color;
    border-radius: @border-radius-base;
    background: #fff;
    color: @gray-color;
    text-align: center;
    font-size: @font-size-base;
    line-height: @height - 2px;
    transition: all 0.3s linear;

    &:hover,
    &:active,
    &:focus {
      border-color: @primary-color;
      color: @primary-color;
      outline: none;
    }
  }

  a& {
    &,
    &:hover,
    &:active {
      text-decoration: none;
    }
  }

  //不同类型
  &-outline {
    border-color: @btn-outline-border-color;
    background: @btn-outline-bg;
    color: @btn-outline-color;

    &:hover,
    &:active,
    &:focus {
      border-color: darken(@btn-outline-border-color, 10%);
      color: darken(@btn-outline-color, 10%);
    }
  }

  &-primary {
    .btn(@btn-primary-border-color, @btn-primary-bg);
  }

  &-error {
    .btn(@btn-error-border-color, @btn-error-bg);
  }

  &-disabled {
    &,
    &:hover,
    &:active,
    &:focus {
      border-color: @btn-disabled-border-color;
      background: @btn-disabled-bg;
      color: @btn-disabled-color;
      cursor: @cursor-disabled;
    }
  }

  &-link,
  a&-link {
    border-color: transparent;
    background: transparent;
    color: @btn-link-color;

    &:hover,
    &:active,
    &:focus {
      border-color: transparent;
      background: transparent;
      color: @btn-link-color;
      text-decoration: underline;
    }

    &.@{btn-prefix-cls}-disabled {
      &:extend(.@{btn-prefix-cls}-disabled all);
      color: @gray-color;
      cursor: @cursor-disabled;

      &:hover,
      &:active,
      &:focus {
        color: @gray-color;
        text-decoration: none;
      }
    }
  }

  // 带图标
  span + .@{iconfont-css-prefix}-down {
    margin:0 -10px 0 10px;
    font-size: 12px;
  }
  .@{iconfont-css-prefix} + span {
    margin-left: 10px;
  }

  //不同尺寸
  &-xs {
    padding: @btn-padding-xs;
    height: @height-xs;
    line-height: @height-xs - 2px;
    font-size: @font-size-base;

    span + .@{iconfont-css-prefix}-down {
      margin: 0 -5px 0 5px;
      .iconfont-size-under-12px(10px);
    }

    .@{iconfont-css-prefix} + span {
      margin-left: 5px;
    }
  }

  &-sm {
    padding: @btn-padding-sm;
    height: @height-sm;
    line-height: @height-sm - 2px;
    font-size: @font-size-base;

    span + .@{iconfont-css-prefix}-down {
      margin: 0 -5px 0 5px;
      .iconfont-size-under-12px(10px);
    }

    .@{iconfont-css-prefix} + span {
      margin-left: 5px;
    }
  }

  &-lg {
    padding: @btn-padding-lg;
    height: @height-lg;
    font-size: @font-size-lg;
    line-height: @height-lg - 2px;
  }

  //加载中
  &-loading {
    position: relative;
    padding-left: 30px;

    &:after {
      position: absolute;
      left: 10px;
      top: 50%;
      margin-top: -9px;
      display: inline-block;
      animation: loadingCircle 1s infinite linear;
      content:"\e6a1";
      font: 16px/1 'tbdicon';
    }
  }

  //按钮组中增加的图标按钮
  &-extra {
    padding: 0 10px;
  }
}

// 开关按钮
.@{css-prefix}switch {
  & {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    border: 1px solid @switch-border-color;
    background: @switch-bg;
    transition: all 0.3s linear;
    cursor: pointer;
    .switch(@switch-height);

    > i {
      position: absolute;
      right: 0;
      top: 0;
      border-radius: 50%;
      background: #fff;
      box-shadow: 0 1px 2px rgba(95, 190, 170, 0.75);
      transition: all 0.3s linear;
    }
  }

  &-off {
    border-color: @switch-disabled-border-color;
    background: @switch-disabled-bg;

    > i {
      right: 100%;
      margin-right: -(@switch-height - 2px);
      box-shadow: 0 1px 2px rgba(154, 164, 171, 0.75);
    }
  }

  &-sm {
    .switch(@switch-height-sm);

   &.@{css-prefix}switch-off {
     > i {
       margin-right: -(@switch-height-sm - 2px);
     }
   }
  }
}

// 按钮组
.@{btn-prefix-cls}-group {
  & {
    &:extend(.clearfix all);
    display: inline-block;
  }

  .@{btn-prefix-cls} {
    float: left;
    margin-left: -1px;
    border-radius: 0;

    &:first-child {
      margin-left: 0;
      border-top-left-radius: 2px;
      border-bottom-left-radius: 2px;
    }
    &:last-child {
      border-top-right-radius: 2px;
      border-bottom-right-radius: 2px;
    }
    &:hover {
      position: relative;
      z-index: 2;
      border-color: @primary-color;
      color: @primary-color;
    }

    &-primary {
      border-left-color: lighten(@btn-primary-border-color, 8%);

      &:first-child {
        border-left: @btn-primary-border-color;
      }
      &:hover {
        color: #fff;
      }
    }

    &-disabled {
      &:hover {
        border-color: @btn-disabled-border-color;
        color: @btn-disabled-color;
        background: @btn-disabled-bg;
      }
    }
  }

  //不同尺寸
  &-xs {
    .@{btn-prefix-cls} {
      padding: @btn-padding-xs;
      height: @height-xs;
      line-height: @height-xs - 2px;
      font-size: @font-size-base;
    }
  }

  &-sm {
    .@{btn-prefix-cls} {
      padding: @btn-padding-sm;
      height: @height-sm;
      line-height: @height-sm - 2px;
      font-size: @font-size-base;
    }
  }
}
