.btn(@border-color, @bg){
  border-color: @border-color;
  background: @bg;
  color: #fff;

  &:hover,
  &:active,
  &:focus {
    border-color: darken(@border-color, 10%);
    background: darken(@bg, 10%);
    color: #fff;
  }
}

.switch(@height){
  & {
    width: @height * 2px;
    height: @height;
    border-radius: floor(@height / 2);

    > i {
      width: @height - 2px;
      height: @height - 2px;
    }
  }
}
