@tag-prefix-cls: ~"@{css-prefix}tag";

.@{tag-prefix-cls} {
  & {
    display: inline-block;
    box-sizing: border-box;
    line-height: @height;

    span {
      position: relative;
      display: block;
      padding: 0 15px;
      box-sizing: border-box;
      .ellipsis;
      border-radius: @border-radius-base;
      color: @base-color;
      cursor: pointer;
      max-width: 150px;

      &:hover {
        color: @primary-color;
      }
    }

    .@{iconfont-css-prefix}-cross {
      display: none;
      position: absolute;
      right: 0;
      top: 0;
      width: 30px;
      line-height: inherit;
      text-align: center;

      &:hover {
        font-weight: bold;
      }
    }
  }

  &-active {
    span,
    span:hover {
      background: @primary-color;
      color: #fff;
    }

    .@{iconfont-css-prefix}-cross {
      display: block;
    }
  }

  &-with-close {
    &.@{tag-prefix-cls}-active span {
      padding-right: 30px;
    }
  }

  // 列表
  &-list {
    margin: 0 -5px;
    .clearfix;

    .@{tag-prefix-cls} {
      float: left;
      margin: 0 5px 10px;
    }

    .@{input-prefix-cls}-wrap {
      float: left;
      width: 130px;
    }

    // 不同尺寸
    &-sm {
      .@{tag-prefix-cls} {
        line-height: @height-sm;
      }

      .@{input-prefix-cls},
      .@{input-prefix-cls}-wrap-extra {
        .input(@height-sm, @font-size-base);
      }
    }
  }

  // 列表组
  &-group {
    .clearfix;

    &-title {
      float: left;
      width: 60px;
      line-height: @height-sm;
    }

    &-content {
      display: block;
      margin-left: 60px;
    }
  }
}
