@notification-prefix-cls: ~"@{css-prefix}notification";

.@{notification-prefix-cls} {
  position: fixed;
	right: 30px;
	top: 30px;
	z-index: 1000;

  &-notice {
    position: relative;
  	margin-bottom: 20px;
  	width: 335px;
  	background: #fff;
  	border: 1px solid @border-color;
  	box-shadow: @box-shadow;
  	border-radius: @border-radius-base;
  	padding: 15px;
    color: @gray-color;

    &-close {
      position: absolute;
  		right: 15px;
  		top: 10px;
  		color: @gray-color;
  		font:18px/1.5 arial;
      text-decoration: none;

  		&:hover {
  			color: @base-color;
        text-decoration: none;
  		}
    }

    &-title {
      margin-bottom: 4px;
  		font-size: 14px;
      font-weight: normal;
  		color: @base-color;
    }

    &-icon {
      position: absolute;
  		left: 15px;
  		top: 50%;
  		margin-top: -16px;
      font-family: 'tbdicon';
  		font-size: 32px;
      font-style: normal;
  		line-height: 1;
    }

    &-content {
      &.with-icon {
        padding-left: 50px;
      }
    }

    // 不同状态
    &.status-info {
      .@{notification-prefix-cls}-notice-icon {
        color: @info-color;

    		&:after {
          content: '\e638';
        }
      }
    }

    &.status-success {
      .@{notification-prefix-cls}-notice-icon {
        color: @success-color;

    		&:after {
          content: '\e630';
        }
      }
    }

    &.status-error {
      .@{notification-prefix-cls}-notice-icon {
        color: @error-color;

    		&:after {
          content: '\e632';
        }
      }
    }

    &.status-warning {
      .@{notification-prefix-cls}-notice-icon {
        color: @warning-color;

    		&:after {
          content: '\e635';
        }
      }
    }
  }
}
