@import "form.less";

@label-prefix-cls: ~'@{css-prefix}label';
@checkbox-prefix-cls: ~'@{css-prefix}checkbox';
@radio-prefix-cls: ~'@{css-prefix}radio';

// 多选框、单选框
.@{checkbox-prefix-cls} {
  display: inline-block;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  border: 1px solid @radio-border-color;
  border-radius: @border-radius-base;
  background: #fff;
  cursor: pointer;

  &:after {
    display: none;
    position: absolute;
    top: 0px;
    left: 3px;
    box-sizing: border-box;
    width: 6px;
    height: 9px;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    content: '';
    transform: rotate(45deg);
  }
}

.@{radio-prefix-cls} {
  display: inline-block;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  border: 1px solid @radio-border-color;
  border-radius: 50%;
  background: #fff;
  cursor: pointer;

  &:after {
    display: none;
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -4px 0 0 -4px;
    width: 8px;
    height: 8px;
    background: @radio-checked-border-color;
    border-radius: 50%;
  }
}

.@{label-prefix-cls} {
  & {
    display: inline-block;
    vertical-align: middle;
    margin-right: @gutter-sm;
    cursor: pointer;
    line-height: 2;
    white-space: nowrap;
  }

  &-text {
    display: inline-block;
    vertical-align: middle;
    color: @base-color;
  }

  .@{checkbox-prefix-cls},
  .@{radio-prefix-cls} {
    position: relative;
    top: -1px;
    vertical-align: middle;

    + .@{label-prefix-cls}-text {
      margin-left: 5px;
    }
  }

  input[type=checkbox],
  input[type=radio] {
    display: none;
  }

  &:hover {
    .@{label-prefix-cls}-text {
      color: @form-hover-color;
    }

    .@{checkbox-prefix-cls},
    .@{radio-prefix-cls} {
      border-color: @radio-hover-border-color;
    }
  }

  // 不同状态
  &-checked,
  &-checked:hover {
    .@{label-prefix-cls}-text {
      color: @radio-checked-color;
    }

    .@{checkbox-prefix-cls},
    .@{radio-prefix-cls} {
      position: relative;
      border-color: @radio-checked-border-color;

      &:after {
        display: block;
      }
    }

    .@{checkbox-prefix-cls} {
      background: @radio-checked-bg;
    }
  }

  &-disabled,
  &-disabled:hover {
    cursor: @cursor-disabled;

    .@{label-prefix-cls}-text {
      color: @radio-disabled-color;
    }

    .@{checkbox-prefix-cls},
    .@{radio-prefix-cls} {
      border-color: @radio-disabled-border-color;
      background: @radio-disabled-bg;
      cursor: @cursor-disabled;
    }

    .@{checkbox-prefix-cls}:after {
      border-color: @radio-disabled-border-color;
    }

    .@{radio-prefix-cls}:after {
      background: @radio-disabled-border-color;
    }
  }

  // 垂直排列
  &-vertical {
    display: block;
    margin-right: 0;
  }
}
