@import "../mixins/form.less";

@input-prefix-cls: ~"@{css-prefix}input";

// input
.@{input-prefix-cls} {
  &,
  &[type=text],
  &[type=url],
  &[type=password],
  &[type=email],
  &[type=search] {
    display: inline-block;
    box-sizing: border-box;
    padding: @input-padding;
    border: 1px solid @form-border-color;
    border-radius: @border-radius-base;
    width: 100%;
    .input(@height, @font-size-base);
    color: @base-color;
    .placeholder(@gray-color);

    &:focus {
      .focus;
    }

    &[disabled] {
      .disabled;
    }
  }
  //不同尺寸
  &-sm {
    &,
    &[type=text],
    &[type=url],
    &[type=password],
    &[type=email],
    &[type=search] {
      .input(@height-sm, @font-size-base);
    }
  }

  &-lg {
    &,
    &[type=text],
    &[type=url],
    &[type=password],
    &[type=email],
    &[type=search] {
      padding: @input-padding-lg;
      .input(@height-lg, @font-size-lg);
    }
  }

  // 文本组合中的添加的文本
  &-extra {
    display: inline-block;
    box-sizing: border-box;
    padding: @input-padding;
    border: 1px solid @form-border-color;
    border-radius: @border-radius-base;
    .input(@height, @font-size-base);
    line-height: @font-size-base * 1.5;
    background: @input-extra-bg;
    color: @base-color;
    white-space: nowrap;
  }
}

// 文本框组合
.@{input-prefix-cls}-group {
  position: relative;
  display: inline-table;
  border-collapse: separate;
  font-size: @font-size-base; // 防止外层行高与字体大小的影响
  line-height: 1.5;

  .@{input-prefix-cls}-extra {
    display: table-cell;
    width: 1%;
  }

  .@{input-prefix-cls} {
    &,
    &-extra {
      border-radius: 0;

      &:first-child {
        border-top-left-radius: @border-radius-base;
        border-bottom-left-radius: @border-radius-base;
      }
      &:last-child {
        border-top-right-radius: @border-radius-base;
        border-bottom-right-radius: @border-radius-base;
      }
    }
  }

  // 与Select结合
  .@{select-prefix-cls} {
    width: 100%;

    .@{select-prefix-cls}-selection {
      border-radius: 0;
    }
    &:first-child .@{select-prefix-cls}-selection {
      border-top-left-radius: @border-radius-base;
      border-bottom-left-radius: @border-radius-base;
    }
    &:last-child .@{select-prefix-cls}-selection {
      border-top-right-radius: @border-radius-base;
      border-bottom-right-radius: @border-radius-base;
    }
  }

  // 不同尺寸
  &-sm {
    .@{input-prefix-cls} {
      &,
      &-extra {
        .input(@height-sm, @font-size-base);
      }
    }

    .@{select-prefix-cls}-selection-single {
      height: @height-sm;
      .@{select-prefix-cls}-selection-rendered {
        line-height: @height-sm - 2px;
      }
    }
  }
}

.@{input-prefix-cls}-wrap {
  display: inline-block;
  position: relative;
  width: 100%;
  font-size: @font-size-base; // 防止外层行高与字体大小的影响
  line-height: 1.5;

  &-extra {
    position: absolute;
    right: 0;
    top: 0;
    box-sizing: border-box;
    padding: @input-padding;
    .input(@height, @font-size-base);
    border: 1px solid transparent;
    border-top-right-radius: @border-radius-base;
    border-bottom-right-radius: @border-radius-base;
    color: @gray-color;
  }

  button.@{input-prefix-cls}-wrap-extra {
    border-color: transparent;
    border-left-width: 0;
    background-color: #fff;
    background-color: transparent\9; //ie8-9
    background-clip: padding-box;
    cursor: pointer;
    transition: all .3s linear;

    :root & {
      background-color: #fff; // reset ie9 and above
    }

    &:hover {
      color: @primary-color;
    }
  }

  // 搜索框
  &-search {
    .search-radius;

    button.@{input-prefix-cls}-wrap-extra {
      font-weight: bold;
    }
  }

  // 不同尺寸
  &-sm {
    .@{input-prefix-cls} {
      &,
      &-wrap-extra {
        .input(@height-sm, @font-size-base);
      }
    }

    &.@{input-prefix-cls}-wrap-search {
      .search-radius(@height-sm);
    }
  }
  &-lg {
    .@{input-prefix-cls} {
      &,
      &-wrap-extra {
        .input(@height-lg, @font-size-lg);
      }
      & {
        padding: 3px 20px;
      }
    }

    &.@{input-prefix-cls}-wrap-search {
      .search-radius(@height-lg);
    }
  }
}

// 文本域
textarea.@{input-prefix-cls} {
  height: auto;
}

.@{css-prefix}form-item-required:before {
  display: inline-block;
  margin-right: 4px;
  content: "*";
  font-family: SimSun;
  font-size: @font-size-base;
  color: @label-required-color;
}

// 表单排列
.@{css-prefix}form-item {
  font-size: @font-size-base;
  margin-bottom: @form-item-margin-bottom;
  box-sizing: border-box;

  &-row {
    > [class*="tbd-col-"] {
      padding-right: 8px;
    }
  }

  &.@{css-prefix}form-item-with-help {
    margin-bottom: @form-item-margin-bottom - @font-size-base * @line-height-base;
  }

  &.@{css-prefix}form-item-only-text {
    margin-bottom: 0;

    > label {
      padding-top: 0;
    }

    .@{css-prefix}form-item-control {
      line-height: 1.5;
    }

    .@{css-prefix}form-text {
      padding-top: 0;
    }

    .@{css-prefix}switch {
      margin: 0;
    }
  }

  > [class*="tbd-col-"] {
    padding-right: 8px;
  }

  > label {
    color: @label-color;
    text-align: right;
    vertical-align: middle;
    padding-right: 5px;
    padding-top: 9px;
    padding-bottom: 9px;
  }

  .@{css-prefix}switch-sm {
    margin: 8px 0;
  }

  .@{input-prefix-cls}-wrap {
    vertical-align: middle;
  }
  textarea.@{input-prefix-cls} {
    vertical-align: middle;
  }

  .@{css-prefix}label {
    padding-top: 6px;
  }
}

.@{css-prefix}form-footer {
  margin-top: @form-item-margin-bottom;

  .tbd-btn {
    margin-right: 10px;
  }
}

.@{css-prefix}form-explain {
  line-height: 1.5;
  margin-top: 3px;
  color: @gray-color;

  &-inline {
    margin-top: 0;
    padding: 9px 0;
  }
}

.@{css-prefix}form-text {
  display: inline-block;
  padding: 9px 8px 9px 0;
  color: @base-color;
  line-height: @line-height-base;
}

.@{css-prefix}form-split {
  display: block;
  text-align: center;
  line-height: 36px;
}

//== Horizontal Form
.@{css-prefix}form-horizontal {
  .@{css-prefix}form-item,
  .@{css-prefix}form-item-row {
    .make-row;
  }
}

//==detail form
.@{css-prefix}form-detail {
  .@{css-prefix}form-item > label {
    color: @gray-color;
  }
}

//== Inline Form
.@{css-prefix}form-inline {
  margin-bottom: 20px;

  .@{css-prefix}form-item {
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    margin-bottom: 0;

    &-with-help {
      margin-bottom: 24px;
    }

    > div {
      display: inline-block;
      vertical-align: middle;
    }
  }

  .pull-right > .@{css-prefix}form-item {
    margin-left: 10px;
    margin-right: 0;
  }

  .@{css-prefix}input {
    display: inline-block;
    width: auto;
    vertical-align: middle;

    &-wrap {
      display: inline-block;
      width: auto;
      vertical-align: middle;

      .@{css-prefix}input {
        width: 100%;
      }
    }

    &-group {
      .@{css-prefix}input {
        width: 100%;
      }
    }
  }

  .@{css-prefix}form-text {
    display: inline-block;
  }

  .has-feedback {
    display: inline-block;
  }

  .@{css-prefix}label {
    margin-right: 0;
  }
}

.@{css-prefix}form-horizontal,
.@{css-prefix}form-inline {
  label {
    > input[type="radio"],
    > input[type="checkbox"] {
      margin-right: 4px;
    }
  }
}

// Validation state
.has-success,
.has-warning,
.has-error,
.is-validating {
  &.has-feedback:after {
    position: absolute;
    top: 0;
    right: 0;
    font-family: "tbdicon" !important;
    .square(@height-lg);
    line-height: @height-lg;
    text-align: center;
    font-size: 14px;
    .animation(zoomIn .3s @ease-out-back);
  }
}

.has-success {
  &.has-feedback:after {
    animation-name: diffZoomIn1 !important;
  }
}

.has-error {
  &.has-feedback:after {
    animation-name: diffZoomIn2 !important;
  }
}

.has-warning {
  &.has-feedback:after {
    animation-name: diffZoomIn3 !important;
  }
}

.has-success {
  .form-control-validation(@success-color; @success-color;);

  &.has-feedback:after {
    content: '\e62f';
    color: @success-color;
  }
}

.has-warning {
  .form-control-validation(@warning-color; @warning-color;);

  &.has-feedback:after {
    content: '\e634';
    color: @warning-color;
  }


}

.has-error {
  .form-control-validation(@error-color; @error-color;);

  &.has-feedback:after {
    content: '\e634';
    color: @error-color;
  }
}

.is-validating {
  &.has-feedback:after {
    display: inline-block;
    .animation(loadingCircle 1s infinite linear);
    content: "\e6a1";
  }
}

// need there different zoom animation
// otherwise won't trigger anim
@keyframes diffZoomIn1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes diffZoomIn2 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes diffZoomIn3 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
