// 可编辑的表格
.@{bcss-prefix}edit-table {
  margin-bottom: 10px;

  .@{table-prefix-cls} {
    margin-bottom: 10px;
  }

  //select 组合
  &-select-group {
    white-space: nowrap;

    > .@{select-prefix-cls} {
      margin-right: 5px;
    }

    > .@{input-prefix-cls} {
      margin-right: 5px;
      width: 110px;
    }

    > .@{input-prefix-cls}-group {
      margin-right: 5px;
      width: 110px;
    }

    .@{tooltip-prefix-cls} {
      width: 180px;
    }
  }
}
